import React, { Component } from 'react';
import { connect } from 'react-redux';
import * as beersActions from '../store/beers/actions';
import * as beersSelectors from '../store/beers/reducer';
import BeersView from '../components/BeersView';

class BeersContainer extends Component {
  constructor(props) {
    super(props);
    this.toggleFavorite = this.toggleFavorite.bind(this);
  }

  componentDidMount() {
    this.props.fetchBeers();
    this.props.getCachedFavoritedBeers();
  }

  toggleFavorite(id) {
    if (this.props.favoritedBeerIds.indexOf(id) === -1) {
      this.props.addToFavorites(id);
    } else {
      this.props.removeFromFavorites(id);
    }
  }

  render() {
    console.log(this.props.favoritedBeerIds);
    return (
      <BeersView beers={this.props.ratedBeers} onHeartClick={(id) => this.toggleFavorite(id)} />
    );
  }
}

const mapStateToProps = (state) => {
  const [beersArray, beersById, beerIdsArray] = beersSelectors.getBeers(state);
  return {
    beersArray,
    beersById,
    beerIdsArray,
    ratedBeers: beersSelectors.getRatedBeers(state),
    favoritedBeerIds: beersSelectors.getFavoritedBeerIds(state),
    favoritedBeersById: beersSelectors.getFavoritedBeersById(state)
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    fetchBeers: () => dispatch(beersActions.fetchBeers()),
    addToFavorites: (beerId) => dispatch(beersActions.addToFavorites(beerId)),
    removeFromFavorites: (beerId) => dispatch(beersActions.removeFromFavorites(beerId)),
    getCachedFavoritedBeers: () => dispatch(beersActions.getCachedFavoritedBeers())
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(BeersContainer);
