import React, { Component } from 'react';
import FeatherIcon from './FeatherIcon';

function BeerCard(props) {
  return (
    <div className="card">
      <div className="card__header">
        <input
          type="checkbox"
          id={`item-${props.beer.id}`}
          className="card__input-favorite"
          name="favorites"
          defaultChecked={props.beer.isFavorite}
        />
        <label
          htmlFor={`item-${props.beer.id}`}
          className="card__label-favorite"
          onClick={() => props.onHeartClick(`${props.beer.id}`)}
        >
          <FeatherIcon name="heart" />
        </label>
      </div>
      <div className="card__body">
        <div className="card__img-wrapper">
          <img className="card__img" src={props.beer.imageURL} alt={props.name} />
        </div>
      </div>
      <div className="card__footer">
        <h4 className="card__title">{props.beer.name}</h4>
        <p className="card__tagline">{props.beer.tagline}</p>
      </div>
    </div>
  );
}

export default function BeersView(props) {
  const beerCards = props.beers.map((beer) => (
    <BeerCard key={beer.id} beer={beer} onHeartClick={(id) => props.onHeartClick(id)} />
  ));
  const hasEvenCards = props.beers.length % 2 === 0;
  return (
    <div className={`card-grid ${hasEvenCards ? 'has-even-cards' : 'has-odd-cards'}`}>
      {beerCards}
    </div>
  );
}
